<template>
   <div class="pay">
        <div class='mine_top'>
            <div class='return' @click='retreat()'></div>
            <span>交易订单</span>
       </div>
       <div class='pay_cen'>
           <div class='pay_but'
           :key='index' 
           v-for='(item,index) in arr'
           @click='change(index)'
           :class='{current:index === showIndex}'>
               {{item.title}}
           </div>
           <div>

           </div>
       </div>
       <components class='muban' :is='Tab'></components>
   </div>
</template>

<script>

import All from './Pay/All'
import Await from './Pay/Await'
import Confirm from './Pay/Confirm'
import Accomplish from './Pay/Accomplish'

export default {
   name: '',
     components: { All,Await,Confirm,Accomplish },
   data() {
       return {
           arr:[
               { title:'全部' },
               { title:'待交易' },
               { title:'待确定' },
               { title:'已完成' }
           ],
           showIndex:0,
           Tab:All
       }
   },
   methods:{
        retreat(){
            this.$router.go(-1);
        },
        change(index){
            this.showIndex = index;
            if( index === 0 ){
                this.Tab = All;
            }else if( index === 1 ){
                this.Tab = Await;
            }else if( index === 2 ){
                this.Tab = Confirm;
            }else if( index === 3 ){
                this.Tab = Accomplish;
            }
        }
   },

}
</script>

