<template>
   <div class="income">
        <van-list
         v-model="loading"
         :finished="finished"
         finished-text="没有更多了"
         @load="onLoad">
            <cargo :data='list'></cargo>
         </van-list>
   </div>
</template>

<script>

import Cargo from './Cargo';

export default {
   name: 'income',
   components:{ Cargo },
   data() {
       return {
           Data:[],
            list:[],
            page:1,
         loading: false,
         finished: false,
       }
   },
    watch:{
        take(news,old){
            this.page = 1;
            this.finished = false;
            this.list = [];
        }
   },
   methods:{
       getData(){
           let url = '/otc/toBeConfirmedList';
           this.$http.post(url,{
                cmd:'218',
                body:{
                    page:this.page++,
                    accessToken:localStorage.token
                }
            }).then( (res)=>{
                this.Data = res;
            })
       },
        onLoad() {
           this.getData();
            // 异步更新数据
            setTimeout(() => {
                for (let i = 0; i < this.Data.length; i++) {
                    this.list.push(this.Data[i]);  
                }
                // 加载状态结束
                this.loading = false;
                // 数据全部加载完成
                if (this.Data.length < 10) {
                    this.finished = true;
                }
            }, 500);
        },
   },
}
</script>

<style scoped lang='scss'>
.income{
    width: 100%;
    height: 100%;
}
</style>
